@charset "UTF-8";
*,
*::after,
*::before {
  background-repeat: no-repeat;
  box-sizing: inherit;
}

html {
  font-size: 87.5%;
  box-sizing: border-box;
}
@media (min-width: 71.875em) {
  html {
    font-size: 100%;
  }
}

body {
  margin: 0;
  padding: 0;
  color: #121212;
  font-family: var(--font--roboto);
  overflow-x: hidden;
  font-weight: 400;
  position: relative;
  z-index: -1;
}

:root {
  --color--bg: hsl(287, 72%, 59%);
  --color--bd1: hsl(347, 86%, 92%);
  --color--bd2: hsl(156, 64%, 87%);
  --color--bd3: hsl(29, 68%, 84%);
  --color--h2-contact-1: hsl(347, 99%, 70%);
  --color--h2-contact-2: hsl(156, 61%, 58%);
  --color--h2-contact-3: hsl(29, 99%, 62%);
}

:root {
  --font--overpass: "Overpass", sans-serif;
  --font--poppins: "Poppins", sans-serif;
  --font--roboto: "Roboto", sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

h1,
h2,
h4,
h5 {
  font-family: var(--font--overpass);
  font-weight: 700;
}

h1:first-letter,
h3:first-letter,
h4:first-letter,
h5:first-letter,
p:first-letter {
  text-transform: uppercase;
}

h1 {
  font-size: 2.8125rem;
  padding-bottom: 8.875rem;
}

h2 {
  color: var(--color--bg);
  font-size: 1.0625rem;
  text-transform: uppercase;
  padding-bottom: 2.875rem;
  letter-spacing: 0.16875rem;
}

h3 {
  font-size: 1.875rem;
  padding-bottom: 1.25rem;
}

h4 {
  font-size: 1.5rem;
  text-transform: capitalize;
  padding-bottom: 2.3125rem;
}

h5 {
  font-size: 1.125rem;
  padding-bottom: 1.5rem;
}

p {
  opacity: 0.7;
  line-height: 1.6;
}

/* Chỉ cho phép textarea được kéo dãn theo chiều dọc, trông sẽ đẹp hơn, không làm xấu layout */
textarea {
  resize: vertical;
}

/* Xóa bỏ khoảng trắng thừa dưới image */
img {
  vertical-align: middle;
}

/* Cũng để dễ control theo design, nên mình thường reset luôn ul, ol */
ul,
ol {
  list-style: none;
}

/* Tạo hiệu ứng bôi đen text khác biệt hơn mặc định */
::selection {
  background: #0031a2;
  color: #fff;
}

a,
a:visited,
a:hover {
  text-decoration: none;
}

.container-sm {
  max-width: 45rem;
  margin: 0 auto;
}

.container {
  max-width: 73.125rem;
  margin: 0 auto;
}
.container--sml {
  padding-top: 1.875rem;
  padding-right: 1.25rem;
  padding-left: 1.25rem;
}
@media (min-width: 71.875em) {
  .container--sml {
    padding-top: 3.875rem;
    padding-left: 0rem;
    padding-right: 0rem;
  }
}
.container--md {
  padding-top: 1.875rem;
  padding-right: 1.25rem;
  padding-left: 1.25rem;
}
@media (min-width: 71.875em) {
  .container--md {
    padding-top: 12rem;
    padding-left: 0rem;
    padding-right: 0rem;
    padding-bottom: 12rem;
  }
}
.container--lg {
  padding-top: 16.375rem;
  padding-bottom: 16.375rem;
}

@media (max-width: 71.8125em) {
  .hide-for-mobile {
    display: none !important;
  }
}

@media (min-width: 71.875em) {
  .hide-for-desktop {
    display: none !important;
  }
}

.hero {
  background-image: linear-gradient(to bottom, #e5e8f0, #fafcff);
}
@media (min-width: 71.875em) {
  .hero .container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}
.hero__content {
  text-align: center;
}
@media (min-width: 71.875em) {
  .hero__content {
    text-align: left;
    flex: 1;
    order: 1;
  }
}
.hero__content h1 {
  font-size: 4rem;
  padding-bottom: 4.375rem;
}
.hero__content p {
  font-size: 1.5rem;
  padding-bottom: 4.125rem;
}
.hero__image {
  background-image: url("/app/images/hero.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  min-height: 26.25rem;
  margin-top: 1.875rem;
}
@media (min-width: 71.875em) {
  .hero__image {
    flex: 0 0 60%;
    order: 2;
    min-height: 32.5rem;
    margin: 0;
  }
}

.btn {
  display: inline-block;
  font-family: var(--font--overpass);
  font-weight: bold;
  font-size: 1.125rem;
  text-align: center;
  padding: 0.9375rem 2.625rem;
  border: 0.125rem solid var(--color--bg);
  color: #111111;
  border-radius: 0.4375rem;
  transition: 0.4s;
  box-shadow: 0px 10px 18px 0px rgba(0, 0, 0, 0.08);
}
.btn--non-bg:hover {
  border-radius: 0.3125rem;
  color: white;
  background-color: var(--color--bg);
}
.btn--bg {
  font-size: 1.25rem;
  padding: 1.625rem 3.75rem;
  transition: 0.4s;
  border-radius: 0.3125rem;
  color: white;
  background-color: var(--color--bg);
}
.btn--under {
  position: relative;
  border: none;
  padding: 0.75rem 0;
}
.btn--under::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 0;
  height: 0.25rem;
  transition: 0.4s;
}
.btn--under:hover::before {
  width: 100%;
  background-color: var(--color--bg);
}

.background {
  position: relative;
}
.background::before, .background::after {
  content: "";
  position: absolute;
  height: 100%;
  width: 100%;
  top: -42%;
}
.background::before {
  background-image: linear-gradient(313deg, #eef1f7, #fff);
  clip-path: polygon(100% 0, 0 0, 100% 100%);
  right: -45%;
  z-index: -1;
}
@media (min-width: 71.875em) {
  .background::before {
    right: -26%;
  }
}
.background::after {
  background-image: linear-gradient(117deg, #fff, #eef1f7);
  clip-path: polygon(100% 0, 0 0, 0 100%);
  left: -50%;
  z-index: -1;
}
@media (min-width: 71.875em) {
  .background::after {
    left: -11%;
  }
}

.header {
  position: fixed;
  right: 0;
  left: 0;
  margin-right: auto;
  margin-left: auto;
  z-index: 999;
}
.header__content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__logo {
  width: 7.5rem;
}
.header__checkbox {
  display: none;
}
.header__btn-nav {
  height: 1.875rem;
  display: flex;
  align-items: center;
}
.header__icon {
  position: relative;
  background-color: red;
}
.header__icon, .header__icon::before, .header__icon::after {
  width: 3rem;
  height: 2px;
  background-color: var(--color--bg);
  display: inline-block;
}
.header__icon::before, .header__icon::after {
  content: "";
  position: absolute;
  left: 0;
  transition: all 0.2s;
}
.header__icon::before {
  top: -0.7rem;
}
.header__icon::after {
  top: 0.7rem;
}
.header__checkbox:checked + .header__btn-nav .header__icon {
  background-color: transparent;
}
.header__checkbox:checked + .header__btn-nav .header__icon::before {
  top: 0;
  transform: rotate(135deg);
}
.header__checkbox:checked + .header__btn-nav .header__icon::after {
  top: 0;
  transform: rotate(-135deg);
}
.header__link {
  display: inline-block;
  font-family: var(--font--overpass);
  font-weight: 800;
}
.header__link a {
  padding: 0.875rem 1.0625rem;
  color: inherit;
}
.header__button {
  margin: 0 0.9375rem 0 0.8125rem;
}
.header__social {
  display: inline-block;
}
.header__social a {
  padding: 0.9375rem;
}

.feature .container {
  text-align: center;
}
.feature__content {
  max-width: 45rem;
  margin: 0 auto;
}
.feature__grid {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 1.875rem;
  margin: 0 3.75rem;
}
@media (min-width: 40em) {
  .feature__grid {
    margin: 0 1.25rem;
  }
}
@media (min-width: 71.875em) {
  .feature__grid {
    margin: 0;
  }
}
.feature__item {
  padding: 3.3125rem;
  border: 0.3125rem solid var(--color--bd1);
  border-radius: 0.9375rem;
  width: 100%;
}
@media (min-width: 40em) {
  .feature__item {
    width: calc((100% - (30 * 0.0625rem)) / 2);
  }
}
@media (min-width: 71.875em) {
  .feature__item {
    width: calc((100% - (30 * 2 * 0.0625rem)) / 3);
  }
}
.feature__item h4 {
  padding-top: 2.125rem;
  padding-bottom: 3.1875rem;
}
.feature__item p {
  letter-spacing: 0.075rem;
  line-height: 1.4;
}
.feature__item:nth-child(2) {
  border-color: var(--color--bd2);
}
.feature__item:nth-child(3) {
  border-color: var(--color--bd3);
}
.feature__icon {
  width: 5.25rem;
  height: 5.25rem;
}

@media (min-width: 71.875em) {
  .innovative .container {
    display: flex;
    align-items: flex-start;
    gap: 12.125rem;
  }
}
.innovative__content {
  text-align: center;
  margin: 1.875rem;
}
@media (min-width: 71.875em) {
  .innovative__content {
    text-align: left;
    flex: 0 0 40%;
    order: 1;
    margin: 0;
  }
}
.innovative__content h1 {
  padding: 2.625rem;
}
.innovative__content p {
  padding-bottom: 2.5rem;
}
.innovative__images {
  padding-top: 1.875rem;
}
@media (min-width: 71.875em) {
  .innovative__images {
    position: relative;
    flex: 0 0 18rem;
    order: 2;
    margin: 0;
  }
}
.innovative__images .images-1 {
  background: url("/app/images/innovation/1.jpg") no-repeat center right;
  background-size: cover;
  border-radius: 0.9375rem;
  clip-path: polygon(100% 0, 100% 5%, 70% 100%, 0 100%, 0 0);
  transform: translateY(-1.875rem);
  min-height: 18.5rem;
}
.innovative__images .images-2 {
  background: url("/app/images/innovation/2.jpg") no-repeat 0 center;
  background-size: cover;
  border-radius: 0.9375rem;
  clip-path: polygon(30% 0, 100% 0, 100% 100%, 0 100%, 0 95%);
  min-height: 18.5rem;
  position: absolute;
  right: -70%;
  top: 30%;
  width: 100%;
}
.innovative__images .images-2::after {
  content: "";
  position: absolute;
  background: url("/app/images/innovation/2.jpg") no-repeat -16% -5px;
  background-size: cover;
  border-radius: 0.9375rem;
  width: 100%;
  min-height: 18.5rem;
  top: 0;
  left: 0;
}

.service {
  position: relative;
}
.service .container--md {
  position: relative;
  padding-bottom: 18.25rem;
}
.service::before {
  background-image: linear-gradient(90deg, #eef1f7, #fff);
  clip-path: polygon(100% 0, 0 100%, 0 0);
  content: "";
  position: absolute;
  z-index: -1;
  width: 100%;
  left: 0;
  height: 10%;
  top: 0;
}
@media (min-width: 71.875em) {
  .service::before {
    height: 48%;
    width: 100%;
    top: -3%;
  }
}
.service::after {
  content: "";
  height: 10%;
  position: absolute;
  right: 0;
  top: -3%;
  width: 90%;
  z-index: -1;
  background-image: linear-gradient(90deg, #eef1f7 0%, white 100%);
  clip-path: polygon(100% 0, 0 0, 100% 100%);
}
@media (min-width: 71.875em) {
  .service::after {
    height: 56%;
    right: 0;
    top: -25%;
    width: 75%;
    z-index: -1;
  }
}
.service__content {
  text-align: center;
}
@media (min-width: 71.875em) {
  .service__content {
    margin-right: 0;
    text-align: right;
  }
}
.service__grid {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}
@media (min-width: 40em) {
  .service__grid {
    margin: 0.625rem;
    gap: 1.25rem;
  }
}
@media (min-width: 71.875em) {
  .service__grid {
    margin: 0;
    gap: 1.875rem;
  }
}
.service__item {
  text-align: center;
  padding: 2.9375rem;
  box-shadow: 0 0 2.5rem #d6d6d6;
  margin: 1.875rem;
  width: 100%;
  border-radius: 0.9375rem;
}
@media (min-width: 40em) {
  .service__item {
    margin: 0;
    width: calc((100% - (30 * 0.0625rem)) / 2);
  }
}
@media (min-width: 71.875em) {
  .service__item {
    margin: 0;
    width: calc((100% - (30 * 2 * 0.0625rem)) / 3);
  }
  .service__item:nth-child(2), .service__item:nth-child(5) {
    transform: translateY(5.4375rem);
  }
  .service__item:nth-child(3), .service__item:nth-child(6) {
    transform: translateY(10.875rem);
  }
}
.service__item p {
  padding-bottom: 3rem;
  font-family: var(--font--poppins);
  font-weight: 400;
}
.service__icon {
  margin-top: 1.5625rem;
  margin-bottom: 3rem;
  margin-left: auto;
  margin-right: auto;
  width: 5.25rem;
  height: 5.25rem;
}
.service__icon img {
  width: 100%;
}
.service__arrow {
  width: 2.625rem;
  padding-bottom: 1.5625rem;
}

.project {
  position: relative;
  background-image: linear-gradient(130deg, #dfe9f3 0%, white 100%);
  z-index: -1;
  text-align: center;
}
.project::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 70%;
  top: 0;
  left: 0;
  z-index: -1;
  background-image: linear-gradient(90deg, #eef1f7 0%, #f8f9fc 100%);
  clip-path: polygon(100% 0, 0% 100%, 0 0);
}
.project::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  bottom: 0;
  right: 0;
  z-index: -1;
  height: 35%;
  right: 0;
  width: 60%;
  background-image: linear-gradient(90deg, #eef1f7 0%, #f7f9fc 100%);
  clip-path: polygon(100% 100%, 0% 100%, 100% 0);
}
.project__grid {
  margin: 0 1.25rem;
}
@media (min-width: 71.875em) {
  .project__grid {
    margin: 0;
  }
}
@media (min-width: 40em) {
  .project__item {
    display: flex;
    gap: 20px;
  }
}
@media (min-width: 71.875em) {
  .project__item {
    display: flex;
    justify-content: space-evenly;
  }
}
.project__item:not(:last-child) {
  padding-bottom: 12rem;
}
.project__item:nth-child(1) img {
  clip-path: url(#clipPath1);
}
.project__item:nth-child(2) img {
  clip-path: url(#clipPath2);
}
.project__item:nth-child(3) img {
  clip-path: url(#clipPath3);
}
.project__item:nth-child(2) .project__info {
  order: -1;
}
.project__item:nth-child(2) img {
  transform: rotateY(180deg);
}
.project__image {
  position: relative;
  transform: translateY(-1.875rem);
}
.project__image img {
  width: 27.875rem;
  height: 27.875rem;
  object-fit: cover;
  object-position: bottom;
}
.project__image img:hover + svg {
  opacity: 1;
}
.project__image svg {
  position: absolute;
  z-index: -1;
  height: 26.25rem;
  width: 26.25rem;
  border-width: 0.4375rem;
  border-style: solid;
  border-radius: 1.0625rem;
  left: -1.125rem;
  top: -3.125rem;
  opacity: 0;
  transition: 0.3s;
}
@media (min-width: 71.875em) {
  .project__image {
    position: relative;
    order: 1;
  }
}
.project__info {
  text-align: center;
}
@media (min-width: 40em) {
  .project__info {
    text-align: left;
  }
}
@media (min-width: 71.875em) {
  .project__info {
    flex: 0 0 40%;
    order: 2;
  }
}
.project__info h3,
.project__info h5 {
  line-height: 1.75;
  text-align: left;
  font-size: 24px;
}
.project__info p {
  padding-bottom: 3.25rem;
}

.team {
  position: relative;
  text-align: center;
}
.team .container::before {
  content: "";
  position: absolute;
  background-image: linear-gradient(90deg, #eef1f7 0%, #f9fcff 100%);
  opacity: 0.451;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}
.team .container::after {
  content: "";
  position: absolute;
  bottom: 0;
  right: 0;
  height: 40%;
  width: 70%;
  background-image: linear-gradient(90deg, #eef1f7 0%, #f7f9fc 100%);
  opacity: 0.722;
  clip-path: polygon(100% 0, 0% 100%, 100% 100%);
  z-index: -1;
}
.team__grid {
  margin-bottom: 11.25rem;
}
@media (min-width: 40em) {
  .team__grid {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 1.75rem;
  }
}
@media (min-width: 71.875em) {
  .team__grid {
    text-align: left;
  }
}
@media (min-width: 40em) {
  .team__item {
    width: calc((100% - (28 * 0.0625rem)) / 2);
  }
}
@media (min-width: 71.875em) {
  .team__item {
    width: calc((100% - (3 * 28 * 0.0625rem)) / 4);
  }
}
.team svg {
  position: absolute;
  width: 0;
  height: 0;
  left: 0;
  top: 0;
}
.team__item {
  margin-bottom: 1.25rem;
}
@media (min-width: 71.875em) {
  .team__item {
    margin: 0;
  }
}
.team__item img {
  margin-bottom: 1.25rem;
  width: 16.875rem;
  height: 19.5rem;
  object-fit: none;
  object-position: center top;
}
@media (min-width: 71.875em) {
  .team__item img {
    margin-bottom: 2.8125rem;
  }
}
.team__item h4 {
  padding-bottom: 1.375rem;
}
@media (min-width: 71.875em) {
  .team__item:nth-child(2n+1) {
    transform: translateY(-10%);
  }
  .team__item:nth-child(2n) {
    transform: translateY(10%);
  }
}
.team__item:nth-child(2n+1) img {
  clip-path: url(#clipPathn);
}
.team__item:nth-child(2n) img {
  clip-path: url(#clipPathn1);
}
.team__item:nth-child(3) img {
  object-position: 30% top;
}
.team__item:nth-child(4) img {
  object-position: 90% top;
}
.team .testimonial {
  display: flex;
  align-items: flex-end;
  gap: 9.375rem;
  text-align: left;
}
.team .testimonial__carousel {
  position: relative;
  flex: 1;
}
.team .testimonial__content {
  position: relative;
  opacity: 0;
  height: 0;
  transition: 1s;
  overflow: hidden;
}
.team .testimonial__content::before {
  content: "“";
  position: absolute;
  top: -10%;
  left: -20%;
  line-height: 1;
  font-size: 20rem;
  color: #c5dad6;
  font-family: sans-serif;
  z-index: 1;
}
.team .testimonial__content.active {
  height: auto;
  opacity: 1;
  overflow: visible;
}
.team .testimonial__content h1 {
  padding-bottom: 4.125rem;
}
.team .testimonial__content p {
  padding-bottom: 4.625rem;
}
.team .testimonial__content .name {
  display: flex;
  align-items: center;
}
.team .testimonial__content .name h5 {
  padding: 0;
  padding-right: 0.9375rem;
}
.team .testimonial__content .name p {
  padding: 0;
}
.team .testimonial__button {
  position: absolute;
  bottom: -10%;
  left: 0;
  z-index: 40;
}
.team .testimonial__button div {
  display: inline-block;
  background-color: var(--color--bg);
  border-radius: 624.9375rem;
  width: 0.625rem;
  height: 0.625rem;
  margin: 0.3125rem;
  transition: 0.5s;
}
.team .testimonial__button div.active {
  width: 1.5625rem;
}
.team .testimonial__image {
  position: relative;
  flex: 1;
}
.team .testimonial__image img {
  width: 29.375rem;
  height: 37.75rem;
  clip-path: url(#testimonial);
  position: relative;
  z-index: 2;
  object-fit: cover;
  object-position: bottom;
}
.team .testimonial__image svg {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
}
.team .testimonial__image #sss {
  height: 17.25rem;
  width: 17.25rem;
  top: 70%;
  left: -20%;
  z-index: 1;
}

.contact {
  text-align: center;
  position: relative;
}
.contact svg {
  position: absolute;
  bottom: 0;
